@media (min-width: 375px) {
.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left:0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.modal.active {
    pointer-events: all;
    opacity: 1;
 }

.modal__content {
    background-color: white;
    transform: scale(0.5);
    transition: 0.4s all;
    height: 204px;
    width: 340px;
    margin-left: 16px;
    margin-top: 286px;
    margin-bottom: 624px;
    border-radius: 4px;
    margin-right: 19px;
}

.modal__content.active {
    transform: scale(1);
}

.welcome {
    height: 38px;
    width: 100%;
    
    margin-top: 32px;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 38px;
    letter-spacing: 0em;
    text-align: center;
    color: #262626;
}

.goodbye {
    height: 38px;
    width: 260px;
    margin-left: 42px;
    margin-right: 38px;
    margin-top: 8px;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 38px;
    color: #262626;
    text-align: center;
}

.buttonM {
    height: 40px;
    width: 308px;
    margin-left: 16px;
    margin-right: 16px;
    margin-top: 32px;
    margin-bottom: 16px;
    border-radius: 2px;
    padding: 8px, 124px, 8px, 124px;
    background: #1890FF;
    border-radius: 2px;
    color: #FFFFFF;
    font-size: 16px;
    line-height: 24px;
    font-style: normal;
    font-weight: 400;
    text-align: center;
    border: none;
}

.buttonM:hover {
        background: #40A9FF;
    }

.buttonM:active {
        background: #096DD9;
    }

    .closeM {
       display: none;
    }

}

