@media (min-width: 375px) {
    body {
        min-width: 375px;
        height: 1114px;
        background-color:white;
        margin: 0px;
        padding: 0px;
    }

    .error {
        color: red;
        height: 40px;
        width: 343px;
        background: #FFFFFF;
        border: 1px solid #D9D9D9;
        box-sizing: border-box;
        border-radius: 2px;
        margin-left: 16px;
        margin-bottom: 24px;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        text-align: left;
        padding-left: 12px;
        padding-top: 8px;
        padding-bottom: 8px;
        transition: all 0.3s;
    }

    .error__div {
        color: red;
        margin-left: 16px;
        position: relative;
        bottom: 25px;
        transition: all 0.3s;
    }

    .error__div__git{
        color: red;
        margin-left: 16px;
        position: relative;
        bottom: 50px; 
        transition: all 0.3s;
    }

    .errors__radio {
        color: red;
        margin-left: 8px; 
        transition: all 0.3s;   
    }

    .error__input__profile {
        color: red;
        background-color: white;
    }

    h1 {
        padding: 0px;
        margin: 0px;
        margin-left: 16px;
        height: 76px;
        width: 343px;
        margin-top: 108px;
        margin-bottom: 40px;
        color: #262626;
        font-size: 40px;
        font-style: normal;
        font-weight: 500;
        line-height: 38px;
        text-align: left;
    }

    h3 {
        padding: 0px;
