        height: 22px;
        width: 343px;
        margin-left: 16px;
        margin-bottom: 2px;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 22px;
        text-align: left;
    }

    .input {
        height: 40px;
        width: 343px;
        background: #FFFFFF;
        border: 1px solid #D9D9D9;
        box-sizing: border-box;
        border-radius: 2px;
        margin-left: 16px;
        margin-bottom: 24px;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        text-align: left;
        padding-left: 12px;
        padding-top: 8px;
        padding-bottom: 8px;
        transition: all 0.3s;
    }

    .input:hover {
        border: 2px solid #1890FF ;
    }
    .input:default {
        border: 2px solid #1890FF ;
    }
    
     input:focus {
        border: 2px solid #1890FF ;
    }
    

    .load {
        height: 42px;
        width: 343px;
        margin-left: 16px;
        border-radius: 2px;
        border: 1px dashed #D9D9D9;
        background: #F5F5F5;
        text-align: center;
        vertical-align: middle;
        margin-bottom: 40px;
        line-height: 42px;
        transition: all 0.3s;

        &:hover {
            box-shadow: 0 0 4px 0px gray;
            
        }
        &:active {
            background:gray;
        }
    }

    

    .hide{
        position: absolute;
        overflow: hidden;
        z-index: -1;
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
    }

    .load__label {
        position: relative;
    }

    .load__label::before{
        display: block;
        position: absolute;
        width: 15px;
        height: 15px;
        background-image: url(Modal/Vertical.png);
        content: '';
        left: -55px;
        top : 5px;
        background-repeat: no-repeat;
    }

    .load__label::after{
        display: block;
        position: absolute;
        width: 15px;
        height: 15px;
        background-image: url(Modal/Gorizont.png);
        content: '';
        left: -61px;
        top : 12px;
        background-repeat: no-repeat;
    }
    .imgAfter {
        display: inline-block;
        margin-right: 15px;
    }

    .imgBefore {
        display: none;
        position: absolute;
        left: 325px;
        top: 0px;
    }

    .success__load{
        position: relative;
        margin-left: 16px;
        color: #1890FF; 
        margin-top: 24px;
        margin-bottom: 40px;
        width:343px; ;
        
        &:hover {
            background:#E6F7FF;
            .imgBefore {
            display: block;
            }
        }
    }

    .error__load {
        color: red;
    }



