    }

    a{
        text-decoration: underline;
        color: #1890FF;
    }

    .button {
        height: 40px;
        width: 343px;
        margin-left: 16px;
        margin-bottom: 82px;
        border-radius: 2px;
        padding: 8px, 134px, 8px, 134px;
        border: 1px solid #D9D9D9;
        background: #1890FF;
