        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        text-align: center;
        color: #FFFFFF;
        transition: all 0.3s;
    }

    .button:hover {
        background: #40A9FF;
    }

    .button:active {
        background: #096DD9;
    }

    .button:disabled {
        background: #E8E8E8;
        color: #BCBCBC;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        text-align: center;
        height: 40px;
        width: 343px;
        margin-left: 16px;
        margin-bottom: 82px;
        border-radius: 2px;
        padding: 8px, 134px, 8px, 134px;
        border: 1px solid #D9D9D9;
    }
}


