@media (min-width: 375px) {
.modalInfo {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left:0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.modalInfo.active {
    pointer-events: all;
    opacity: 1;
 }

.modalInfo__content {
    height: 500px;
    width: 340px;
    margin-left: 16px;
    margin-right: 19px;
    margin-top: 150px;
    border-radius: 4px;
    background: #FFFFFF;
    margin-bottom: 150px;
    overflow: hidden;

}

.modalInfo__content.active {
    transform: scale(1);
}
.buttonI {
    height: 40px;
    width: 308px;
    margin-left: 16px;
    margin-right: 16px;
    margin-top: 12px;
    margin-bottom: 16px;
    border-radius: 2px;
    padding: 8px, 124px, 8px, 124px;
    background: #1890FF;
    border-radius: 2px;
    color: #FFFFFF;
    font-size: 16px;
    line-height: 24px;
    font-style: normal;
    font-weight: 400;
    text-align: center;
    border: none;
}

.buttonI:hover {
    background: #40A9FF;
}

.buttonI:active {
    background: #096DD9;
}

.div1 {
    height: 76px;
    width: 272px;
    margin-left: 16px;
    margin-top: 16px;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 38px;
    text-align: left;
    color: #262626; 
}

.div2 {
    width: 304px;
    height: 310px;
    margin-left: 16px;
    margin-right: 20px;
    margin-top: 32px;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 38px;
    text-align: left;
    overflow-y: scroll;
    overflow-x: inherit;
}

b {
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 38px;
    text-align: left;
}

.close {
    width: 15px;
    height: 15px;
    position: absolute;
    top: 20px;
    right: 20px;
}
}
